* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Poppins", sans-serif;
  /* font-family: "Signika", sans-serif; */
  user-select: none;
}



*::before,
*::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
}
body {
  height: 100vh;
  background: #161616;
}

.container {
  position: relative;
  height: 100%;
  width: 100%;
  display: grid;
  place-items: center;
}

.container::before {
  top: -80px;
  left: 120px;
  background: linear-gradient(#f0f, #f00);
  clip-path: circle(20% at 25% 30%);
}

.container::after {
  top: 300px;
  left: -50px;
  background: linear-gradient(#e91e63, #2196f3);
  clip-path: circle(20% at 70% 65%);
}

.container .card-wrapper {
  position: relative;
  display: grid;
  place-items: center;
  text-align: center;
  z-index: 1;
  width: 300px;
  height: 300px;
  box-shadow: 20px 20px 50px rgba(0, 0, 0, 0.5);
  border-radius: 15px;
  border-top: 1px solid rgba(255, 255, 255, 0.5);
  border-left: 1px solid rgba(255, 255, 255, 0.5);
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(5px);
  overflow: hidden;
  margin-top: 150px;
}

.container .card-wrapper .content h3 {
  font-size: 2em;
  color: #fff;
}

.container .card-wrapper .content p {
  font-size: 1em;
  color: #fff;
  font-weight: 300;
  font-family: "Signika", sans-serif;
}
.container .card-wrapper .content a {
  display: inline-block;
  padding: 8px 20px;
  margin-top: 15px;
  background: #fff;
  color: #000;
  border-radius: 20px;
  text-decoration: none;
  font-weight: 500;
}
